/* GENERIC */
html, body {
  height: 100%;
  width: 100%;
  background: #EEEEEE;
  overflow-x: hidden;
}

.number {
  font-size: 50px;
}

#loader {
  position: absolute;
  top: 50%;
  left: 50%;
}

#logo-admin {
  background: #2e3c54;
  text-align: center;
  padding-bottom: 20px;
}

#logo-admin img {
  padding-top: 20px;
  width: 95%;
  height: auto;
}

#avatar-admin {
  width: 35px;
  height: auto;
  margin: 15px;
}

.dropdown-button {
  height: 64px;
}

.no-pdl {
  padding-left: 0 !important;
}

.no-pdr {
  padding-right: 0 !important;
}

.no-mb {
  margin-bottom: 0 !important;
}

.divider-custom {
  margin-left: auto;
  margin-right: auto;
  width: 50px;
  height: 1px;
  background-color: #26a69a;
}

.pdb20 {
  padding-bottom: 20px;
}

.pdt20 {
  padding-top: 20px;
}

.ml10 {
  margin-left: 10px;
}

.ml20 {
  margin-left: 20px;
}

.mt5 {
  margin-top: 5px;
}

.mt10 {
  margin-top: 10px;
}

.mt20 {
  margin-top: 20px;
}

.mb10 {
  margin-bottom: 10px;
}

.mb20 {
  margin-bottom: 20px;
}

h1 {
  font-size: 18px;
}

.strong {
  font-weight: bold;
}

#specific-admin-container {
  margin-left: 5px;
  margin-right: 0px;
  width: 100%;
}

#vertical-menu {
  margin: 0;
  background: #2e3c54;
  border: none;
  min-height: 100vh;
}

#vertical-menu .material-icons {
  margin-right: 10px;
}

#vertical-menu .material-icons a {
  color: #FFFFFF;
}

#vertical-menu a {
  color: #FFFFFF;
}

#vertical-menu li:hover {
  background: #35475d;
}

.vertical-menu-item {
  padding: 12px;
}

#horizontal-menu-dashboard {
  background: #FFFFFF;
  box-shadow: 0 3px 2px -2px #A5A5A5;
}

@media screen and (max-width: 992px) {
  #menu-left-admin {
    display: none;
  }
}

.block-home {
  background: #FFFFFF;
  margin-bottom: 5px;
  margin-right: 5px;
  text-align: center;
  height: 200px;
  width: 24.5% !important;
}

#block-table {
  width: 785px;
  background: #FFFFFF;
  height: 350px;
  position: relative;
}

#block-table .pagination {
  position: absolute;
  bottom: 0;
  left: 0;
}

#menu-mobile {
  display: none;
}

.block-lg, .block-md {
  height: 350px;
  width: 390px !important;
  background: #FFFFFF;
  margin-left: 5px !important;
  text-align: center;
}

.block-md {
  height: 172.5px !important;
  position: relative;
}

.stats {
  font-size: 30px;
  position: absolute;
  top: 20%;
  left: 25%;
}

.block-graph {
  height: 300px;
}

@media screen and (max-width: 1250px) {
  .block-home {
    width: 49% !important;
  }
}

@media screen and (max-width: 1250px) {
  .block-home {
    width: 49% !important;
  }
  .block-lg, .block-md {
    width: 49% !important;
    margin-left: 0 !important;
  }
}

@media screen and (max-width: 992px) {
  #container-admin {
    width: 100%;
  }
  #menu-mobile {
    display: block;
  }
}

@media screen and (max-width: 500px) {
  .block-home {
    width: 100% !important;
    margin-left: 0 !important;
    margin-right: 0 !important;
  }
  .block-lg, .block-md {
    width: 100% !important;
  }
}
